import React from 'react';
import CharacterCard from './CharacterCard';

class CharacterList extends React.Component {
  // AQUÃ TENGO QUE MAPEAR

  sortByName(a, b) {
    const nameA = a.name.toUpperCase();
    const nameB = b.name.toUpperCase();

    let comparison = 0;
    if (nameA > nameB) {
      comparison = 1;
    } else if (nameA < nameB) {
      comparison = -1;
    }
    return comparison;
  }

  render() {
    console.log(this.props.resultStore);

    this.props.resultStore.sort(this.sortByName);
    if (this.props.resultStore.length === 0) {
      return <p className="main__page--no-results">{`No hay ningÃºn personaje que coincida con la palabra ${this.props.name}`}</p>;
    } else {
      return (
        <ul className="main__page--list">
          {this.props.resultStore.map((character) => {
            return <CharacterCard key={character.id} character={character} />;
          })}
        </ul>
      );
    }
  }
}

export default CharacterList;
